body, html {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

#app, #advertising {
  height: 100%;
  width: 100%;
  color: #fff;
}

#advertising {
  position: relative;
}

#advertising-title {
  text-align: center;
  width: 100%;
  text-transform: uppercase;
  padding: 25px 0;
  font-size: 2em;
  font-weight: bold;
  position: absolute;
  top: 0;
}

#advertising-image {
  height: 100%;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

